import React from 'react';
import styles from './LiquidityTitle.module.css'

function LiquidityTitle() {
  return (
    <div className={styles.title}>
      <div className={styles.method}>DMS</div>
        <div className={styles.text}>
            <p>TokenDMS</p>
        </div>
    </div>
  );
}

export default LiquidityTitle;
