import React from 'react';
import styles from './RewardTitle.module.css'

function RewardTitle() {
  return (
    <div className={styles.title}>
        <div className={styles.method}>NFT</div>
        <div className={styles.text}>
            <p>NFT<span>()</span></p>
        </div>
    </div>
  );
}

export default RewardTitle;
