import React from 'react';
import styles from './TransferModal.module.css'
import ModalButtom from '../../Button/ModalButtom'

function TransferModal(props: { openClick: any }) {
   
    const {openClick} = props

  return (
    <div className={styles.modal}>
        <div className={styles.bg}>
          <div className={styles.flexName}>
            <div className={styles.nameImage}></div>
            <div className={styles.nameData}>
              <h3></h3>
              <p><span>25311</span></p>
              <p><span>110</span></p>
              <p><span>1</span></p>
            </div>
          </div>
          <div className={styles.address}>
            <p></p>
            <input></input>
          </div>
          <div className={styles.flexBtn}>
            <div className={styles.cancelBtn}>
                <ModalButtom onClick={ () => openClick(0)} name="" color="green"/>
            </div>
            <div className={styles.transferBtn}>
                <ModalButtom onClick={ () => openClick(0)} name="" color="orange"/>
            </div>
          </div>
        </div>
    </div>
  );
}

export default TransferModal;
